   import commItem from '@/pages/playPage/commentItem.vue';
   import chatinput from '@/pages/playPage/chatinput.vue';
   import uniPopup from '@/pages/playPage/uni-popup.vue';
   var util = require('../../common/util.js');
	export default {
		components: {
			commItem,chatinput,uniPopup
		},
		onResize(){
			this.bigInputBlur();
		},
		onHide() {
			this.videoContext.pause();
		},
		onShow() {
			this.videoContext.play();
		},
		data() {
			return {
				scrollLeft:0,
				baseDateName:"98TV",
				baseDataSrc :'_doc/test.db',
				type: '',
				select_status:false,
				playTvSrc: "",
				playTvImg:"",
				vodId:0,
				allsuccessData: {},
				tabIndex:0,
				collectionNum:'',
				otherContentList:[],
				otherCommentListShow:false,
				commentListShow:false,// 是否有评论
				currentCommentType:1,
				currentCommentId:0,
				currentCommentMid:0,
				currentCommentIndex:0,
				loadSurvey:true,// 是否改变组件的值
				loadComplent:false,
				danmuList: [],
				commentData: [],
				name:'xcecd@qq.com',
						style: {
							pageHeight: 0,
							detailViewHeight: 0,
							contentViewHeight: 0,
							commentViewHeight:0,
							footViewHeight: 90,
							mitemHeight: 0,
						},
				scrollTop: 0,
				messages: [{
					user: 'home',
					type: 'head', //input,content 
					content: '你好!'
				}]
			}
		},
		onLoad(options) {
			this.videoContext = uni.createVideoContext('myVideo')
            // console.log(options.vodId);
			this.vodId = options.vodId;
			const res = uni.getSystemInfoSync();
			this.style.pageHeight = res.windowHeight;
			this.style.contentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (650); //像素
			this.style.detailViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (540); //像素
			this.style.commentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (640); //像素
			if(this.$store.state.userInfo.userId){
				var uid = this.$store.state.userInfo.userId;
			}else{
				var uid ='';
			}
			uni.request({
				url: this.websiteUrl + '/api/home/play?vodId='+this.vodId + '&userId=' + uid ,
				success: (res) => {
					console.log(res.data);
					this.allsuccessData = res.data;
					if(this.allsuccessData){
						this.playTvImg = this.allsuccessData.vodPic;
						this.playTvSrc = this.allsuccessData.vodPlayUrl[0].playUrl;
						this.currentCommentMid = this.allsuccessData.typeId1;
						if(this.allsuccessData.vodPlayUrl && this.allsuccessData.vodPlayUrl.length>1){
							this.select_status = true;
						}else{
							this.select_status = false;
						}
						if(this.allsuccessData.commentList){
							this.commentData = this.allsuccessData.commentList;
						}else{
							this.commentData = [];
						}
						if( this.commentData.length >0){
							this.commentListShow =false;
						}else{
							this.commentListShow =true;
						}
						if(this.allsuccessData.isCollect){
							this.loadComplent  =true;
						}else{
							this.loadComplent  =false;
						}
					}	
				}
			});
		},
		
		// 退出该页面
		onBackPress(){
			var   self  = this ;
			if(this.$store.state.isLogin){
				// console.log(this.$store.state.userInfo.loginToken);
				uni.request({
					url: this.websiteUrl +'/user/add/visit',
					method:"POST",
					header:{Authorization:this.$store.state.userInfo.loginToken},
					data:{
						"userId":this.$store.state.userInfo.userId,
						"vodId":Number(this.vodId),
						"vodType":this.allsuccessData.typeId1,
						"partsNo":this.tabIndex,
						"partsUrl":this.allsuccessData.vodPlayUrl[this.tabIndex].playUrl,
						"playTime":"15:25"},
					success: (res) => {
						console.log(res.data);
					}
				});
			}else{
				if(!plus.sqlite.isOpenDatabase({name:self.baseDateName ,path:self.baseDataSrc})){
					plus.sqlite.openDatabase({
						name: self.baseDateName,
						path: self.baseDataSrc,
						success: function(e){
							console.log('openDatabase success!');
							self.executeSQL();
						},
						fail: function(e){
							console.log('openDatabase failed: '+JSON.stringify(e));
						}
					});
				}else{
					self.executeSQL();
				}
			}
			
		},
		methods: {
			async toXuanji(e) { //点击tab-bar
				let tabIndex = e.currentTarget.id;
				this.playTvSrc = this.allsuccessData.vodPlayUrl[tabIndex].playUrl;
					if (this.tabIndex === tabIndex) {
					return false;
				} else {
					this.tabIndex = tabIndex;
					if(this.tabIndex >=5 && this.type === 'selected_works' ){
						this.scrollLeft = 6.5* (this.tabIndex + 3)
					}
				}
			},
			// 播放结束
			videoEnd(){
				if(this.select_status){
					this.tabIndex++;
					this.playTvSrc = this.allsuccessData.vodPlayUrl[this.tabIndex].playUrl;
					this.videoContext.play();
				}
			},
			gotoFeedback(){
				uni.navigateTo({
					url: '../../pages/feedback/feedback'
				})
			},
			togglePopup(type) {
				this.type = type;
				this.currentCommentType =1;
				this.currentCommentMid = this.allsuccessData.typeId1;
			},
			// 显示其他评论
			checkOtherContent(index,comment,type){
				this.currentCommentIndex = index;
				this.type = type;
				this.currentCommentType =2;
				this.currentCommentId = comment.commentId;
				this.currentCommentMid = comment.commentMid;
				if( this.allsuccessData.commentList && this.allsuccessData.commentList[index] && this.allsuccessData.commentList[index].commentObject){
					this.otherContentList = this.allsuccessData.commentList[index].commentObject;
				}else{
					this.otherContentList = [];
				}
				console.log(this.otherContentList.length);
				if(this.otherContentList.length > 0 ){
					this.otherCommentListShow=false;
				}else{
					this.otherCommentListShow=true;
				}
				
			},
			toSendComent(data){// 添加评论
			    let  self  = this;
				if(this.currentCommentType == 1){
					this.currentCommentId = 0;
				}
				// self.commentData =[];
				uni.request({
					url: this.websiteUrl +'/api/comment/save', //仅为示例，并非真实接口地址。
					method:"POST",
					data:{
							"commentId":Number(this.currentCommentId),
							"userId":Number(this.$store.state.userInfo.userId),
							"commentName":this.$store.state.userInfo.userName,
							"commentMid":Number(this.currentCommentMid),
							"commentRid":Number(this.vodId),
							"commentContent":data.inputValue
							},
						success: (res) => {
							console.log(res.data);
							var  requesData = { commentContent: data.inputValue,
												commentId: res.data,
												commentMid: Number(this.currentCommentMid),
												commentName: this.$store.state.userInfo.userName,
												commentObject:[],
												commentPid:this.currentCommentId,
												commentTime:util.getNowTime('yyyy-MM-dd hh:mm:ss'),
												userId:Number(this.$store.state.userInfo.userId),
												};
												 console.log(requesData);
							 if(self.currentCommentType == 1 ){
								self.commentData.push(requesData);	
								self.commentListShow = false;
							 }else{
								 console.log(2222);
								 console.log(requesData);
								self.commentData[this.currentCommentIndex].commentObject.push(requesData);
								this.commentListShow = true;
								this.$nextTick(()=>{
								 this.commentListShow = false;
								})
								this.otherCommentListShow = false;
								console.log(this.commentData);
							 }	
					}
				});
		},
			videoErrorCallback: function(e) {
			// uni.showModal({
			// 	content:"视频加载出错",
			// 	// showCancel: false
			// })
			},
			goBack(){
				uni.navigateBack({
				delta: 1,
				animationType: 'pop-out',
				animationDuration: 200
			})
			},
			bigInputFocus(){
				const res = uni.getSystemInfoSync();
				this.style.contentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (1250); //像素
				this.style.detailViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (1210); //像素
				this.style.commentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (1100); //像素
			},
			bigInputBlur(){
				const res = uni.getSystemInfoSync();
				this.style.contentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (650); //像素
				this.style.detailViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (540); //像素
				this.style.commentViewHeight = res.windowHeight - uni.getSystemInfoSync().screenWidth / 750 * (640); //像素
			},
			toAddLike(){
				if(this.collectionNum ==''){
					console.log(111111);
					uni.request({
						url: this.websiteUrl +'/user/add/collect',
						method:"POST",
						header:{Authorization:this.$store.state.userInfo.loginToken},
						data:{
							"userId":this.$store.state.userInfo.userId,
							"vodId":Number(this.vodId),
							"vodType":this.allsuccessData.typeId1,
							"partsNo":this.tabIndex,
							"partsUrl":this.allsuccessData.vodPlayUrl[this.tabIndex].playUrl,
							"playTime":"10:25"
							},
						success: (res) => {
								console.log(res.data);
								    this.collectionNum = res.data;
									if(this.collectionNum){
										this.loadComplent  =true;
											this.loadSurvey = false;
											this.$nextTick(()=>{
												this.loadSurvey = true
											})
											uni.showToast({
											title: '收藏成功',
											duration: 1000
											});
									}else{
										uni.showToast({
										title: '收藏失败',
										duration: 1000
										});
									}	
						}
					});
				}else{
					console.log(2222);
					uni.request({
						url: this.websiteUrl +'/user/del/collect',
						method:"POST",
						data:[Number(this.collectionNum)],
						success: (res) => {
							this.collectionNum='';
							this.loadComplent  =false;
							 // this.$refs.chatinput.collectImage = false;
							 this.loadSurvey = false;
							 this.$nextTick(()=>{
							 	this.loadSurvey = true
							 })
							uni.showToast({
							title: '取消收藏',
							duration: 1000
						});
						}
					});
				}
					
			},
			toDownLoad(){
				
				
			},
			
			//  创建数据库
			executeSQL(){
				var  self  = this;
				plus.sqlite.executeSql({
					name: self.baseDateName,
					sql: 'create table if not exists history("vodId" INT(10) PRIMARY KEY,"vodBlurb" CHAR(255),"vodName" CHAR(255),"vodPic" CHAR(255),"partsNo" INT(10),"partsUrl" CHAR(255) ,"playTime" CHAR(255) )',
					success: function(e){
						console.log('executeSql success!');
						var sql="replace into history('vodId','vodBlurb','vodName','vodPic','partsNo','partsUrl','playTime') values("+self.vodId+",'"+self.allsuccessData.vodBlurb+"','"+self.allsuccessData.vodName+"','"+self.allsuccessData.vodPic+"',"+self.tabIndex+",'"+self.allsuccessData.vodPlayUrl[self.tabIndex].playUrl+"','12:25')"
						console.log(sql);
						plus.sqlite.executeSql({
							name: '98TV',
							sql: sql,
							success: function(e){
								console.log('executeSql success!');
								self.selectSQL();
							},
							fail: function(e){
								console.log('executeSql failed: '+JSON.stringify(e));
							}
						});
					},
					fail: function(e){
						console.log('executeSql failed: '+JSON.stringify(e));
					}
				});
			},
			// 添加到数据库
			 selectSQL(){
				 var  self = this;
				plus.sqlite.selectSql({
					name: self.baseDateName,
					sql: 'select * from  history',
					success: function(data){
						console.log('selectSql success: ');
						// for(var i in data){
						// 	console.log(data[i]);
						// }
						self.closeDB();
					},
					fail: function(e){
						self.closeDB();
						console.log('selectSql failed: '+JSON.stringify(e));
					}
				});
			},
			// 关闭数据库
			 closeDB(){
				 var  self  = this;
				plus.sqlite.closeDatabase({
					name: self.baseDateName,
					success: function(e){
						console.log('closeDatabase success!');
					},
					fail: function(e){
						console.log('closeDatabase failed: '+JSON.stringify(e));
					}
				});
			},
			toShare(){
				uni.switchTab({
					url: '../../pages/tarbar/share/share'
				})
			}
	  }
	}
